<template>
  <div id="select">
    <p>{{displayResponse}}</p>
    <p v-if="user && user.name">Hello {{user.name}}</p>
    <div id="client_list" v-if="clientList">
      <div v-for="client in clientList">
         Account  <router-link :to="{name: 'Checklist',  params: { customerId: client.id }}">{{ client.name }}</router-link>, ID {{ client.id}}
      </div>
    </div>
    <span v-else> Loaiding accounts list</span>
  </div>
</template>
<script>
import axios from 'axios'

export default {
  data () {
    return {
      displayResponse: null,
      user: {},
      clientList: null
    }
  },
  methods: {
    getUserProperties () {
      const path = '/api/get_user'
      axios.get(path)
      .then(response => {
        console.log(response.data);
        this.user = response.data
      })
      .catch(error => {
        console.log(error)
        this.$router.push('/')
      })
    },
    getClientList () {
      const path = '/api/get_client_list'
      axios.get(path)
      .then(response => {
        console.log(response.data);
        this.clientList = response.data
      })
      .catch(error => {
        console.log(error)
        // this.$router.push('/')
      })
    }
  },
  created () {
    this.getUserProperties()
    this.getClientList()
  }
}

</script>
<style>
  div#client_list {
    text-align: left;
    display: inline-block;
  }
</style>



